
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  scroll-behavior: smooth;
  width: 100%;
} 

  #body {
    width: 100%;
  }



.navbar {
  display: flex;
  justify-content: space-between;
  position: fixed;
  height: 100px;
  z-index: 1;
  background-color: black;
  width: 100%;
}

.name{
  color: whitesmoke;
  width: 45%;
  contain: content;
  display: flex;
  justify-content:flex-start;
  align-items: center;
}

.name h2{
  font-size: 20px;
  text-align: center;
  font-family: cursive;
  z-index: 1;
  
}

.links{
  width: 45%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  color: lightgray;
  margin-right: 5%;
}

.links a{
  color: lightgray;
  font-family:'Courier New', Courier, monospace;
  font-weight: bold;
  text-decoration: none;
}



.intro {
  height: 600px;
  width: 100%;
  background-image: url(./images/jungle.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  contain: content;

}

.intro_body{
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.intro_header{
  text-align: center;
  color: rgb(188, 247, 203);
  font-size: 50px;
  position: relative;
  top: 150px;
  font-stretch: expanded;
  font-family:fantasy;
}

.intro_content{
  width: 40%;
  min-width: 300px;
  margin: 5%;
  margin-bottom: 5%;
}

.intro_content h2{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 32px;
  color:rgb(252, 255, 164);
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black;
}

.bio{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap-reverse;
  padding: 20% 0px;
  background-color:black;
}


.bio h1{
  text-align: center;
  font-family: fantasy;
  font-size: 60px;
  color: rgb(177, 175, 175);
}

.bio_body{
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
}



.bio_content{
  margin-right: 10%;
  margin-top: 10%;
  min-width: 30%;
  max-width: 40%;
  color: rgb(236, 235, 235);
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: larger;
}

.bio_content span{
  color:#61dafb;
}

.bio_image img{
  border-radius: 5%;
  margin-right: 50px;
}


.code{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20%;
  background-image: url(./Backdrops/pawel-czerwinski-2dyR13FNg2I-unsplash.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.code h2{
  font-size: 30px;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: rgb(66, 0, 110);
}



.code p{
  text-align: center;
  padding: 10px;
  margin: 10px 10px;
  background-color: rgb(1, 2, 90);
  border-radius: 20px;
  color: white;
}



.code_content{
  width: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-items: center;
  flex-direction: column;
}

.code_content div{
  display: flex;
  justify-content: center;
  width: 100%;
}


.projects{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: whitesmoke;
  background-color: rgb(255, 255, 255);
}

.projects_section h1{
  text-align: center;
  font-family: fantasy;
  font-size: 60px;
  color: rgb(100, 1, 84);
  
}

.project1{
  background-color: #61dafb;
}

.project2{
  background-color: #61dafb;
}

.project3{
  background-color:#61dafb;
}


.project_header{
  text-align: center;
  font-size: 20px;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: rgb(3, 3, 1);
}

.project_content{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 10% 5%;
  flex-wrap: wrap;
}

.project_info { 
  min-width: 300px;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 3%;
}

.project_info a{
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(0, 30, 199);
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
}

.project_info img{
  margin-right: 10px;
}

.project_info ul{
  color: black;
  font-family: monospace;
  font-size: 14px;
  font-weight: bolder;
}


.project1_images{
  display: inline-block;
  border-radius: 5%;
  border: 1px border black;
  height:400px;
  width: 700px;
  background-image: url(./images/dashboardPage.png), url(./images/intropage.png), url(./images/regLogPage.png), url(./images/lessonPage.png);
  background-position: left top, right top, left bottom, right bottom;
  background-size: 350px;
  background-repeat: no-repeat,no-repeat,no-repeat,no-repeat;
}

.project2_images {
  border-radius: 5%;
  border: 1px solid black;
  height: 400px;
  width: 700px;
  background-image: url(./images/proViceDash.png), url(./images/askQuestion.png), url(./images/allQuestions.png), url(./images/qustionAnswer.png);
  background-position: left top, right top, left bottom, right bottom;
  background-size: 350px;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
}

.project3_images {
  border-radius: 5%;
  border: 1px solid black;
  height: 400px;
  width: 700px;
  background-image: url(./images/mario.png);
  background-size: 800px;
  background-repeat: no-repeat;
}

.contact{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.contact_header{
  color: whitesmoke;
  font-size: xx-large;
  font-family: fantasy;
}

.contact_body{
  display: flex;
  justify-content: center;
  width: 100%;
}

.contact_content{
  margin: 20px;
  max-width: 600px;
  min-width: 200px;
  font-size: larger;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: rgb(248, 222, 73);
}

.contact_form{
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.contact_form a{
  display: flex;
  justify-content:flex-start;
  align-items: center;
  color: whitesmoke;
  text-decoration: none;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 900;
}

.contact_form img{
  background-color: black;
  margin-right: 20px;
}

.github{
  margin-top: 5px;
}









